<template>
  <button 
    class="reaction"
    :aria-label="reactions.text"
    @click="sendMessage(reactions)"
    @keyup.enter="sendMessage(reactions)"
  >
   <img :src=reactions.icon :alt=reactions.text >
   <span class="tooltip">{{reactions.text}}</span>
  </button>
</template>

<script>
import { generateUUID } from "../../../utils/index";
  const HandupIcon="data:image/png;base64,";
  const agreeIcon="data:image/png;base64,";
  const disagreeIcon ="data:image/png;base64,";
  const respondIcon = "data:image/png;base64,";
  const clarifyIcon = "data:image/png;base64,";
  const pooIcon = "data:image/png;base64,";
  const blockIcon="data:image/png;base64,";
  const opposeIcon="data:image/png;base64,";
export default {
  props: {
     reaction: String
  },
  computed: {
    reactions() {
      let text
      let icon
      switch (this.reaction) {
        case 'Handup':
          icon = HandupIcon
          text = 'I want to speak'
          break;
       case 'Agree':
          icon = agreeIcon
          text = 'I agree'
          break;
       case 'Disagree':
          icon = disagreeIcon
          text = 'I disagree'
          break;

      case 'Respond':
          icon = respondIcon
          text = 'I want to respond'
          break;
      
      case 'Clarify':
          icon = clarifyIcon
          text = 'I need clarification'
          break;

      case 'Point of Order':
          icon = pooIcon
          text = 'I want to Raise a Point of Order'
          break;

      case 'Block':
          icon = blockIcon
          text = 'I block'
          break;

      case 'Oppose':
          icon = opposeIcon
          text = 'I oppose'
          break;
      
        default:
          break;
      }

      return {icon, text}
    },
    canPostMessage() {
      return this.$store.state.messages.filter((h) => h.owner === true).length < 1;
    },
    canRaiseHand() {
      return this.$store.state.hands.filter(h => h.owner === true).length < 1;
    },
    canRespond() {
      return this.$store.state.response.filter(h => h.owner === true).length < 1;
    },
    getUsername() {
      if (this.$store.state.isFullName) {
        return this.$store.getters.getUser("fullName");
      } else {
        return this.$store.getters.getUser("name");
      }
    },
  },
  methods: {
    sendMessage(reaction) {
      const id = generateUUID()

      const reactionTypes = {}
      this.reaction ==='Respond' ? (
        reactionTypes.dispatchType = "addRespond",
        reactionTypes.action = "RESPONDQUEUE",
        reactionTypes.allowed = this.canRespond
      ): 
      this.reaction ==='Handup' ? (
        reactionTypes.dispatchType = "addHand",
        reactionTypes.action = "QUEUEHAND",
        reactionTypes.allowed = this.canRaiseHand
      ):
      (
        reactionTypes.dispatchType = "addMessage",
        reactionTypes.action = "MESSAGE",
        reactionTypes.allowed = this.canPostMessage
      )

      this.$store.dispatch("closeDropdown", "reactions");
      if (reactionTypes.allowed) {
        this.$store.dispatch(reactionTypes.dispatchType, {
          messageId: id,
          encoded: reaction.icon,
          username: this.getUsername,
          img: this.$store.getters.getUser("avatar"),
          owner: true
        });
        this.$socket.sendObj({
          action: reactionTypes.action,
          message: {
            id: this.$store.getters.getUser("meetingID"),
            encoded: reaction.icon,
            username:  this.getUsername,
            img: this.$store.getters.getUser("avatar"),
            messageId: id
          },
        });
      }
    },
  },
}

</script>

<style scoped>

  .tooltip {
    display: none;
    position: absolute;
    background: white;
    padding: 5px;
    top: 66px;
    left: -32px;
    z-index: 2;
    width: 100px;
    border-radius: 4px;
  }

</style>